    * {
      margin: 0;
      padding: 0;
      box-sizing: border-box;
    }

    ul {
      list-style: none;
    }

    body {
      background: black;
    }


    main {
      width: 1200px;
      display: grid;
      grid-template-columns: 40% 60%;
      margin: 10px auto;
      position: relative;
    }

    .canvas-wrapper{
      display: flex;
      justify-content: center;
      padding-top: 5px;
      padding-bottom: 0px;
      position: relative;
    }

    h3{
        font-family: 'Dancing Script', cursive;
        font-size: 28px;
        color: white;
      }

    h2{
      font-family: 'Dancing Script', cursive;
      font-size: 32px;
    }

   
 /* Game Panel*/
 .canvas-wrapper{
    display: flex;
    justify-content: center;
    padding-top: 5px;
    padding-bottom: 0px;
    position: relative;
  }

  #piano-keys {
    display: flex;
    width: 100%;
    justify-content: center;
    position: relative;
  }

  .player-key {
    height: 220px;
    width: 70px;
    border: 1px solid black;
    background: #FAF9F1;
    border: outset;
    display: flex;
    align-items: flex-end;
    justify-content: center;

    > label {
      padding-bottom: 15px;
      color: #51514D;
      font-size: 18px;
    }
  }

    .black-key {
    height: 120px;
    width: 50px;
    background: black;
    border: outset grey;
    margin: 0 -25px;
    z-index: 5;
  }

  .blue.selected {
    border: inset;
    background: #33FCFF;
  }

  .pink.selected {
    border: inset;
    background: #AF33FF;
  }

  .yellow.selected {
    border: inset;
    background: #E5FE15;
  }

  .orange.selected {
    border: inset;
    background: #FF8D33;
  }

  .green.selected {
    border: inset;
    background: #AFFF33;
  }


  #timer, #score{
    font-size: 30px;
    width: 150px;
  }

  .timer-container{
    margin-top: 50px;
  }

  .score-container{
    margin-top: 50px;
  }

  .game-info{
    color: white;
    text-align: center;
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
  }

  #quit, #try-again {
    border: 2px solid white;
    outline: none;
    margin-top: 15px;
    color: white;
    background: black;
    font-family: 'Dancing Script', cursive;
    font-size: 22px;
  }

  #quit{
    height: 40px;
    width: 70px;
  }

  #try-again{
    display: block;
    height: 40px;
    width: 150px;
  }

  .flashing{
    animation: flash 1s 2;
  }

  @keyframes flash {
    0% {opacity: 1;}
    49% {opacity: 1;}
    50% {opacity: 0;}
    99% {opacity: 0;}
  }

  
  .game-over-warning.hidden{
    display: none;
  }

  #try-again.hidden{
    visibility: hidden;
  }

  .game-over-warning{
    top: -85px;
  }

  .game-over-warning{
    display: block;
    position: absolute;
    width: 300px;
    height: 60px;
    line-height: 60px;  
    text-align: center;
    background: black;
    z-index: 10;
    color: white;
    border: white 1px solid;
    
  }

  /*Instructions Panel*/
  .left-panel{
  background: black;
  color: white;
  border-right: 1px solid white;
  margin-top: 5px;
  padding-right: 5px;
  
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}

.instructions-panel{
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  padding-right: 10px;
}


#left-title{
  width: 90%;
  text-align: center;
  text-decoration: underline;
  font-size: 40px;
}

 .links{

    ul {
      display: flex;
    }
      
      a {
        text-decoration: none;
        margin-right: 15px;
        display: flex;
        flex-direction: column;
        align-items: center;
        
       > label {
         font-size: 12px;
       }

       > label:hover{
         cursor: pointer;
       }
      }
    

    .icon {
      color: white;  
    }
  }

  /*Game Menu*/
   #menu-background{
      position: absolute;
      top: 0;
      left: 40%;
      width: 60%;
      height: 100%;
      display: block;
      background: black;
      z-index: 100;
    }

   
  #menu-background.hidden{
    display: none;
  }

    #title, #splash-title{
      font-size: 60px;
      font-family: 'Dancing Script', cursive;
      color: white;
      width: 500;
    }

    #open-title{
      margin-bottom: 100px;
    }

    #title, #splash-title{
      position: absolute;
      top: 250px;
      transform: rotate(-10deg);
      text-shadow:
      -1px -1px 0 #000,
      1px -1px 0 #000,
      -1px 1px 0 #000,
      1px 1px 0 #000;
    }
    

    .levels, .song-titles{
      color: white;
      display: flex;
      flex-direction: column;
      align-items: center;
      height: 40px;
    }

    input[type="radio"]{
      display: none;
    }

   input[type="radio"] + label{
     position: relative;
     margin-top: 15px;
     border: 1px solid white;
     text-align: center;     
     width: 100px;
     padding: 5px;

   }

   input[type="radio"]:checked + label, input[type="radio"] + label:hover, 
   #play-button:hover, #quit:hover, #try-again:hover {
     background: white;
     color: black;
   }

    input[type="radio"]:checked + label::before{
      background-image: url(../assets/images/selection.jpg);
      position: absolute;
      left: -25px;
      top: 0px;
      background-size: 25px 25px;
      content: "";
      height: 25px;
      width: 25px;
    }

    #big-logo, #title-logo{
      height: 300px;
      width: auto;
    }

    .menu-container{
      height: 100%;
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;;
      justify-content: space-evenly;
      padding: 40px 0;
    }

    .menu-form {
      width: 80%;
      text-align: center;
    }

    .selection {
      display: flex;
      justify-content: space-between;
      margin: 80px 20px 168px 20px;
    }

    #play-button{
      outline: none;
      width: 90px;
      height: 40px;
      font-family: 'Dancing Script', cursive;
      border: 1px solid white;
      font-size: 28px;
      background: black;
      color: white;
    }

    #sound-control{
      position: absolute;
      top: 20px;
      right: 0;
      color: white;
    }

    /*Splash*/

    #splash-modal-background{
      position: fixed;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background: black;
      width: 100%;
      height: 100%;
      z-index: 500;
    }

    .splash-modal-content{
      width: 100%;
      text-align: center;
      position: relative;
    }

    #splash-modal-background.hidden{
      display: none;
    }

    #splash-title{
      width: 100%;
      top: 200px;
    }

  
